<template>
  <div id="Feedback">
    <van-tabs v-model="active" :sticky="sticky" :swipe-threshold="threshold" color="#FF8D59" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
      <van-tab title="流程">
        <flow></flow>
      </van-tab>
      <van-tab title="费用">
        <cost></cost>
      </van-tab>
      <van-tab title="取车">
        <upcar></upcar>
      </van-tab>
      <van-tab title="用车">
        <usedcars></usedcars>
      </van-tab>
      <van-tab title="还车">
        <dropCars></dropCars>
      </van-tab>
      <van-tab title="租车">
        <rentCars></rentCars>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
    const flow = () => import("./SubPage/flow.vue");
    const cost = () => import("./SubPage/cost.vue");
    const upcar= () => import("./SubPage/upCar.vue");
    const usedcars= () => import("./SubPage/usedCars.vue");
    const dropCars= () => import("./SubPage/dropCars.vue");
    const rentCars= () => import("./SubPage/rentCars.vue");
    export default {
        components:{
          flow,
          cost,
          upcar,
          usedcars,
          dropCars,
          rentCars
        },
          data() {
            return {
              active: 0,
              sticky:true,
              threshold:6
            }
          },
          methods:{

          },
        }
</script>

<style scoped  lang="scss">

#Feedback{


}
</style>



